/**
 * This descriptor is used to describe a set of flags stored as a `boolean` value.
 */
export class BooleanDescriptor {
    /**
     * @inheritdoc
     */
    get name() {
        return "boolean";
    }
    /**
     * @inheritdoc
     */
    get defaultValue() {
        return false;
    }
    /**
     * @inheritdoc
     */
    hasFlag(value, flag) {
        return !flag || value;
    }
    /**
     * @inheritdoc
     */
    addFlag(value, flag) {
        return value || flag;
    }
    /**
     * @inheritdoc
     */
    removeFlag(value, flag) {
        return value && !flag;
    }
}
