/**
 * This descriptor is used to describe a set of flags stored as a `bigint` value.
 */
export class BigIntDescriptor {
    /**
     * @inheritdoc
     */
    get name() {
        return "bigint";
    }
    /**
     * @inheritdoc
     */
    get defaultValue() {
        return 0n;
    }
    /**
     * @inheritdoc
     */
    hasFlag(value, flag) {
        return (value & flag) === flag;
    }
    /**
     * @inheritdoc
     */
    addFlag(value, flag) {
        return value | flag;
    }
    /**
     * @inheritdoc
     */
    removeFlag(value, flag) {
        return value & ~flag;
    }
}
