/**
 * Null logger implementation, used for discarding all log messages.
 */
export class NullLogger {
    /**
     * @inheritdoc
     */
    fatal(_message) {
        // NOP
    }
    /**
     * @inheritdoc
     */
    error(_message) {
        // NOP
    }
    /**
     * @inheritdoc
     */
    warn(_message) {
        // NOP
    }
    /**
     * @inheritdoc
     */
    info(_message) {
        // NOP
    }
    /**
     * @inheritdoc
     */
    debug(_message) {
        // NOP
    }
}
