/**
 * This descriptor is used to describe a set of flags stored as a `number` value.
 */
export class NumberDescriptor {
    /**
     * @inheritdoc
     */
    get name() {
        return "number";
    }
    /**
     * @inheritdoc
     */
    get defaultValue() {
        return 0;
    }
    /**
     * @inheritdoc
     */
    hasFlag(value, flag) {
        return (value & flag) === flag;
    }
    /**
     * @inheritdoc
     */
    addFlag(value, flag) {
        return value | flag;
    }
    /**
     * @inheritdoc
     */
    removeFlag(value, flag) {
        return value & ~flag;
    }
}
